<template>
  <v-card id="SymptomsDialogCard">
    <v-card-title class="title pa-0" :class="color+'--text'">
      <v-avatar tile color="white" size="50">
        <v-icon medium :color="color">visibility</v-icon>
      </v-avatar>
      <span>{{ symptom.name }}</span>
      <v-spacer></v-spacer>
      <slot></slot>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="subheading">{{ symptom.description }}</v-card-text>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['color', 'symptom'],
  data () {
    return {
    }
  }
}
</script>



